.drop-down {
  position: relative;
  display: inline;
}
.drop-down-selection {
  width: 50px;
  padding: 5px;
  left: 0px;
  background-color: #bf9949;
  color: #ffffff;
  border: none;
  text-align: center;
}

.drop-down-content {
  display: none;
  position: absolute;
  left: 0px;
  z-index: 1;
}
.drop-down-content button {
  display: block;
  width: 50px;
  padding: 5px;
}

.option {
  padding: 3px;
  border: none;
  background-color: #EAe7dc;
  color: #E85A4F;
}
.option:hover {
  background-color: #bbb9b0;
}

.radio-toggle__container {
  display: flex;
  position: relative;
  overflow: hidden;
  justify-content: center;
  margin-top: 15px;
}

.radio-toggle__container input {
  position: absolute;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  width: 1px;
  border: 0;
  overflow: hidden;
}

.radio-toggle__container label {
  background-color: #e4e4e4;
  color: rgba(0, 0, 0, 0.6);
  font-size: 14px;
  line-height: 1;
  text-align: center;
  padding: 8px 16px;
  margin-right: -1px;
  border: 1px solid rgba(0, 0, 0, 0.2);
  box-shadow: inset 0 1px 3px rgba(0, 0, 0, 0.3), 0 1px rgba(255, 255, 255, 0.1);
  transition: all 0.1s ease-in-out;
}
.radio-toggle__container label:hover {
  cursor: pointer;
}

.radio-toggle__container label:first-of-type {
  border-radius: 4px 0 0 4px;
}

.radio-toggle__container label:last-of-type {
  border-radius: 0 4px 4px 0;
}

.radio-selected {
  background-color: #bf9949 !important;
}

.drop-down:hover .drop-down-content {
  display: block;
}
.info-label {
  margin-right: 10px;
  color: #ffffff;
  font-weight: 700;
}

@media screen and (max-width: 600px) {
  .info-item {
    text-align: center;
  }
}


.nav-list {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
}
.list-item {
  display: inline-block;
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase;
  border-radius: 5px;
  margin-right: 15px;
  margin-top: 5px;
  padding: 7px 20px;
}

.nav-link {
  text-decoration: none;
  color: #ffffff;
  font-weight: bold;
  position: relative;
}
.nav-link:before {
  content: "";
  position: absolute;
  width: 0;
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: #ffffff;
  visibility: hidden;
  transition: all 0.3s ease-in-out;
}
.nav-link:hover:before {
  visibility: visible;
  width: 100%;
}

.selected {
  font-weight: 800;
  color: #bf9949;
}

@media screen and (max-width: 600px) {
  .nav-link {
    font-size: 14px;
  }
}
