.loader__container {
  position: relative;
  margin: 0 auto;
}

.loader__container > .loader { 
  position: absolute;
  margin: auto;
  top: 0;
  right: 0;
  bottom: 300px;
  left: 0;
  border: 14px solid #ffffff;
  border-radius: 50%;
  height: 120px;
  width: 120px;
  border-top: 14px solid #bf9949;
  -webkit-animation: loader 2s linear infinite;
          animation: loader 2s linear infinite;
}

@-webkit-keyframes loader {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes loader {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
