.main__container {
  width: 250px;
  height: 320px;
  margin: 10px 10px;
}

.card__container {
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  transition: all 0.5s ease;
  position: relative;
}

.card__front {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  background-color: #8e8d8a;
  /*For mozilla*/
  -webkit-backface-visibility: hidden;
}

.card__back {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  transform: rotateY(180deg);
  background-color: #d3d0c6;
  /*For mozilla*/
  -webkit-backface-visibility: hidden;
}

.card__container:hover {
  transform: rotateY(180deg);
}

